.header {
  background: #228be6; }

.header .header-content {
  width: 1400px;
  margin: 0 auto;
  padding-left: 3rem;
  padding-right: 3rem;
  display: flex;
  align-items: center; }

@media (max-width: 1399px) {
  .header .header-content {
    width: 100%; } }

@media (max-width: 767px) {
  .header .header-content {
    padding-left: 1rem;
    padding-right: 1rem; } }

.header .header-content .brand {
  color: white;
  font-size: 1.5rem;
  font-weight: 600; }

.header .header-content .right {
  margin-left: auto; }
